@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Roboto-Regular"), local("Roboto Regular"), url("../../../assets/fonts/Roboto-Regular/Roboto-Regular.woff2") format("woff2"), url("../../../assets/fonts/Roboto-Regular/Roboto-Regular.woff") format("woff"), url("../../../assets/fonts/Roboto-Regular/Roboto-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Roboto-Medium"), local("Roboto Medium"), url("../../../assets/fonts/Roboto-Medium/Roboto-Medium.woff2") format("woff2"), url("../../../assets/fonts/Roboto-Medium/Roboto-Medium.woff") format("woff"), url("../../../assets/fonts/Roboto-Medium/Roboto-Medium.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Roboto-Bold"), local("Roboto Bold"), url("../../../assets/fonts/Roboto-Bold/Roboto-Bold.woff2") format("woff2"), url("../../../assets/fonts/Roboto-Bold/Roboto-Bold.woff") format("woff"), url("../../../assets/fonts/Roboto-Bold/Roboto-Bold.ttf") format("truetype");
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  height: 100%;
  height: stretch;
  font-size: 62.5%;
}

body {
  position: relative;
  min-width: 376px;
  min-height: 100%;
  min-height: stretch;
  padding: 0;
  font-family: var(--font-family);
  font-size: var(--font-size-m);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}

a, a:hover {
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

button,
input,
optgroup,
select,
textarea {
  font: inherit;
}

.hidden {
  display: none !important;
}
@media (max-width: 767px) {
  .hidden_mobile {
    display: none !important;
  }
}
@media (max-width: 1023px) {
  .hidden_mobile-tablet {
    display: none !important;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .hidden_tablet {
    display: none !important;
  }
}
@media (min-width: 1024px) {
  .hidden_desktop {
    display: none !important;
  }
}
@media (min-width: 768px) {
  .hidden_desktop-tablet {
    display: none !important;
  }
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  padding: 0 !important;
  border: 0 !important;
  clip: rect(0, 0, 0, 0) !important;
  clip-path: inset(100%) !important;
}

.theme, .theme_dark {
  --main-color: var(--color-light);
  --dim-color: var(--color-gray);
  color: var(--main-color);
  background-color: var(--color-dark);
  background-image: radial-gradient(70.24% 70.24% at 49.87% 29.76%, rgba(18, 12, 1, 0.9) 0%, #000000 100%);
}
.theme_light {
  --main-color: var(--color-dark);
  --dim-color: var(--color-gray-light);
  color: var(--main-color);
  background-color: var(--color-white);
  background-image: none;
}

.headline {
  font-size: var(--font-size-xl);
  line-height: calc(var(--font-size-xl) + 0.2rem);
  font-weight: 700;
}

.subhead {
  font-size: var(--font-size-l);
  line-height: calc(var(--font-size-l) + 0.2rem);
  font-weight: 700;
}
.subhead_dim {
  color: var(--dim-color);
}

.main-text {
  font-size: var(--font-size-m);
  line-height: calc(var(--font-size-m) + 0.2rem);
  font-weight: 500;
}
.main-text_dim {
  color: var(--dim-color);
}

.caption {
  font-size: var(--font-size-s);
  line-height: calc(var(--font-size-s) + 0.2rem);
  font-weight: 400;
}
.caption_dim {
  color: var(--dim-color);
}

.column-bar {
  background-image: linear-gradient(180deg, var(--color-transparent) 0%, rgba(0, 0, 0, 0.8) 100%), radial-gradient(149.08% 93.3% at 38.75% 72.1%, #000000 0%, #231900 0.01%, #4d4d4d 100%);
  box-shadow: inset -0.1rem 0.1rem 0.1rem rgba(255, 255, 255, 0.2), inset 0.2rem 0.2rem 10.6rem rgba(103, 103, 103, 0.6);
  border-radius: 0.6rem;
}
.column-bar_active {
  background-image: radial-gradient(91.67% 122.17% at 69.17% -11.17%, #ffa300 0%, #2d1c00 100%);
  box-shadow: inset -0.1rem 0.1rem 0.1rem rgba(255, 255, 255, 0.4), inset 0 0.6rem 1.5rem rgba(255, 162, 0, 0.4);
}
.column-bar_wide.column-bar_active {
  background-image: radial-gradient(81.14% 100% at 50% 0%, #ffa300 0%, #2d1c00 100%);
  box-shadow: inset -0.1rem 0.1rem 0.1rem rgba(255, 255, 255, 0.4), inset 0 0.6rem 1.5rem rgba(255, 162, 0, 0.5);
}

.theme_light .column-bar {
  background-image: linear-gradient(180deg, rgba(244, 244, 244, 0.9) 0.82%, #e9e9e9 100%);
  box-shadow: inset -0.1rem 0.1rem 0.1rem rgba(255, 255, 255, 0.5), inset 0 0 2rem rgba(207, 207, 207, 0.5);
}
.theme_light .column-bar_active {
  background-image: linear-gradient(180deg, #fff2d1 0.82%, #ffd66c 100%);
  box-shadow: inset -0.1rem 0.1rem 0.1rem rgba(255, 255, 255, 0.5), inset 0 0 2rem rgba(255, 176, 57, 0.8);
}

.slide {
  max-width: 100rem;
  margin: 0 auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: calc(100 * var(--vh, 1vh));
  padding: 2.4rem;
  padding-top: 2rem;
}
.slide__title {
  margin: 0;
  margin-bottom: 0.8rem;
}
.slide__subtitle {
  margin: 0;
}
.slide_leaders {
  padding-bottom: 0;
}
.slide_leaders .person-card {
  padding-top: 0;
}

@media (min-width: 576px) {
  .slide {
    padding-right: 3.2rem;
    padding-left: 3.2rem;
  }
  .slide__caption {
    text-align: center;
  }
}
.person-card {
  margin: 0;
  padding: 2rem 0.4rem;
  padding-bottom: 1.6rem;
  text-align: center;
}
.person-card_low-position-selected {
  display: flex;
  flex-direction: column;
  padding-top: 0;
  padding-bottom: 0;
}
.person-card_low-position-selected span.person-card__low-selected-position {
  position: static;
  min-width: 6rem;
  order: 1;
  margin-top: 0.9rem;
  padding-top: 0.6rem;
  border-top: 0.1rem solid var(--color-gray);
  transform: none;
}
.theme_light .person-card_low-position-selected span.person-card__low-selected-position {
  border-color: var(--color-dark);
}
.person-card__avatar {
  margin: 0 auto;
  border-radius: 50%;
}
.person-card__emoji {
  display: inline-block;
  font-size: 3.2rem;
  line-height: 3.2rem;
  vertical-align: top;
  transform: translateY(1rem);
}
.person-card__caption {
  line-height: 1;
}
.person-card__full-name {
  margin-top: 0.6rem;
  margin-bottom: 0.4rem;
}
.person-card__name, .person-card__surname {
  display: block;
  font-weight: 500;
  font-size: var(--font-size-m);
}

.theme_light .person-card__result {
  color: var(--color-dark);
}

.leaders-histogram {
  --bar-height-ratio: 48.653;
  margin: 0;
  padding: 0;
  list-style: none;
  counter-reset: leaders;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 0.8rem;
}
.leaders-histogram > li {
  counter-increment: leaders;
}
.leaders-histogram > li::before {
  content: "" counter(leaders) "";
}
.leaders-histogram__bar {
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  transform: scaleX(calc(120 / 104));
}
.leaders-histogram__bar-wrap {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  grid-row: 1/2;
}
.leaders-histogram__bar-wrap::before,
.leaders-histogram__bar-wrap .person-card__low-selected-position {
  font-size: 3.2rem;
  line-height: calc(3.2rem + 0.2rem);
  position: absolute;
  left: 50%;
  z-index: 10;
  font-weight: 700;
  transform: translate(-50%, 100%);
}
.leaders-histogram__bar-wrap:nth-child(1) {
  z-index: calc(10 - 1);
}
.leaders-histogram__bar-wrap:nth-child(1) .leaders-histogram__bar {
  height: calc(var(--bar-height-ratio) * var(--vh, 1vh) - 0 * 5rem);
}
.leaders-histogram__bar-wrap:nth-child(1) .person-card__low-selected-position, .leaders-histogram__bar-wrap:nth-child(1)::before {
  bottom: calc(calc(var(--bar-height-ratio) * var(--vh, 1vh) - 0 * 5rem) - 1.8rem);
}
.leaders-histogram__bar-wrap:nth-child(2) {
  z-index: calc(10 - 2);
}
.leaders-histogram__bar-wrap:nth-child(2) .leaders-histogram__bar {
  height: calc(var(--bar-height-ratio) * var(--vh, 1vh) - 1 * 5rem);
}
.leaders-histogram__bar-wrap:nth-child(2) .person-card__low-selected-position, .leaders-histogram__bar-wrap:nth-child(2)::before {
  bottom: calc(calc(var(--bar-height-ratio) * var(--vh, 1vh) - 1 * 5rem) - 1.8rem);
}
.leaders-histogram__bar-wrap:nth-child(3) {
  z-index: calc(10 - 3);
}
.leaders-histogram__bar-wrap:nth-child(3) .leaders-histogram__bar {
  height: calc(var(--bar-height-ratio) * var(--vh, 1vh) - 2 * 5rem);
}
.leaders-histogram__bar-wrap:nth-child(3) .person-card__low-selected-position, .leaders-histogram__bar-wrap:nth-child(3)::before {
  bottom: calc(calc(var(--bar-height-ratio) * var(--vh, 1vh) - 2 * 5rem) - 1.8rem);
}
.leaders-histogram__bar-wrap:nth-child(3) {
  grid-column: 1/2;
}
.leaders-histogram__bar-wrap:nth-child(1) {
  grid-column: 2/3;
}
.leaders-histogram__bar-wrap:nth-child(2) {
  grid-column: 3/4;
}
.leaders-histogram__bar-wrap:nth-child(even) .leaders-histogram__bar {
  transform-origin: right;
}
.leaders-histogram__bar-wrap:nth-child(odd) .leaders-histogram__bar:not(.leaders-histogram__bar_first) {
  transform-origin: left;
}
.leaders-histogram__bar-wrap:nth-child(n+4):not(.leaders-histogram__bar-wrap_no-number) {
  display: none;
}
.leaders-histogram__bar-wrap_no-number {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 1.6rem;
  z-index: 10;
}
.leaders-histogram li.leaders-histogram__bar-wrap_no-number::before {
  content: none;
}

